@import url("https://fonts.googleapis.com/css2?family=Baloo+2:wght@800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Baloo 2", cursive;
}

body {
  background-color: #000;
}

header {
  background-color: #ddd;
  color: #000;
  width: 100%;
  height: 12vh;
  font-size: 50px;
  padding: 2px;
  text-align: center;
}

main {
  margin-top: 5rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.input-wrapper {
  display: flex;
  flex-direction: column;
}

.input-wrapper > input {
  margin-bottom: 2rem;
  min-height: 1.5rem;
  outline: 0;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid black;
  border-radius: 2rem;
}

.details-wrapper {
  margin-top: 2rem;
  min-width: 30rem;
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
